import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';

export default function ButtonAppBar({ onClick, activeYear }: { onClick: Function, activeYear: number }) {
  const ButtonComponent = ({ year }: { year: number }) => (
    <Button sx={{
      '@media (min-width: 800px)': {
        margin: '0px 30px',
      },
      flexGrow: 1,
      color: activeYear === year ? '#1976d2' : 'white',
      backgroundColor: activeYear === year ? 'white' : '#1976d2',
      '&:hover': {
        color: 'black',
        backgroundColor: activeYear === year ? 'white' : '#1976d2',
      },
    }} onClick={() => onClick(year)}>
      {year}
    </Button>
  )

  const availableYears = [2023, 2022, 2019, 2018, 2017, 2016]

  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar position="static">
        <Toolbar>
          {/* <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={{ mr: 2 }}
          >
            <MenuIcon />
          </IconButton> */}
          <Typography>
            The Archives:
          </Typography>
          {availableYears.map((year: number) => (
            <ButtonComponent year={year}/>
          ))}
        </Toolbar>
      </AppBar>
    </Box>
  );
}